      "Aktive Post ist eine Mischung aus _Activity_ und _Stille Post_.\n\n**Fr 5 Spieler:** Ein Spieler bernimmt die Spielleitung und nennt oder zeigt Spieler Nr. 1 einen Begriff aus zwei zusammengesetzten Wrtern (z.B.: Ameisenhaufen). Nr. 1 muss nun Nr. 2 den Begriff pantomimisch darstellen. Nr. 2 soll nun Nr. 3 den Begriff zeichnen. Nr. 3 erklrt nun Nr. 4 den Begriff und dieser benennt ihn, erst hier wird aufgelst. Nur die Erklrung des jeweiligen Vorgngers darf gesehen / gehrt werden.\n\nFr die einzelnen Schritte ist nur 1 Minute Zeit und der Begriff wird immer weitergegeben ohne wirklich zu wissen, ob er richtig verstanden wurde, erst der Letzte in der Reihe darf mgliche Lsungen nennen.\n\nDie Reihenfolge vom Zeichnen und Pantomime kann auch getauscht werden  das verbale Erklren sollte der Einfachheit halber am Ende stehen (es ist einfacher die Augen zu schlieen oder sich wegzudrehen, als die Ohren zuzuhalten und wirklich nichts zu hren).\n\nBei einer Variante mit **4 Spielern**, denkt sich der jeweils erste den Begriff aus.\n",
