    tags: ["excercise", "Zuschauer auf der Bhne", "Charakter", "Charakter"],
    createdAt: "2022-04-16T20:28:07.569Z",
    url: "https://improwiki.com/de/spiele/amerikanische-sitcom",
    content:
      'Dies ist eine spezielle Spielvariante indem eine amerikanische Sitcom und ihre Eigenheiten improvisiert werden. Solch eine Sitcom kann selbstverstndlich auch mit anderen Vorgaben oder auch als Langform (mit mehr Plotanteil) improvisiert werden.\n\n1 Spieler K spielt das Kind der Familie, 1 Spieler H spielt das Haustier, 1 Spieler N spielt den Nachbar, 1 Spieler M spielt ein Elternteil (Mutter/Vater) und ein Zuschauer, der auf die Bhne geholt wurde, spielt das andere Elternteil (Mutter / Vater).\n\nAls Vorgaben knnen bei diesem Spiel zum Beispiel abgefragt werden:\n\n*   1 Tick/Macke fr das Kind\n*   ein Haustier und ein Trick, den das Haustier beherrscht\n*   eine Nationalitt fr den Nachbar\n*   sowie eine leichte psychologische (Neurose) fr Spieler M (Mutter/Vater)\n*   fr den Zuschauer wird nichts abgefragt\n*   Als letztes wird eine Moral fr die Geschichte abgefragt.\n\nDas Publikum kann nun instruiert werden, mitzumachen und zwar auf folgende Art und Weise (vorher einmal ben):\n\n*   Wenn das Kind seinen Tick/Macke benennt , raunen alle Zuschauer: "Ahhhh"\n*   Wenn das Haustier seinen Trick zeigt, klatschen alle Zuschauer wie wild\n*   Wenn der Nachbar auf die Bhne kommt, rufen die Zuschauer: "Wuf, Wuuf Wuuf".\n*   Wenn Spieler M (Mutter oder Vater) einen Satz beenden, rufen sie "Buuuh".\n*   Wenn der Zuschauer auf der Bhne irgendetwas sagt, lachen die Zuschauer so laut sie knnen\n\nDann wird eine ganz normale Szene gespielt mit allen Empfehlungen, die man im Improtheater so beachten kann. Die Reaktionen der Zuschauer auf die Aktionen der Spieler sind ein Spass, aber quasi auch nur ein Bonus und sollten nicht die ganze Zeit im Mittelpunkt stehen.\n\nZiel ist es, eine Szene zu spielen, eine einfache, kleine Geschichte im Stile der amerikanischen Sitcoms zu erzhlen und am Ende zur vorgebenen Moral zu gelangen.\n\nDieses Spiel sollte nicht zu lange gespielt werden, weil die Zuschauer sonst zu lange gefordert werden und schnell ermden knnten.\n\nDie Szene sollte starten mit dem Wissen, dass das Ziel ist, zur (vorgegebenen) Moral der Geschichte zu gelangen.\n',
