    content:
      "Bei dieser bung trainiert man Wahrnehmung, Gehen, Begegnung, [Status](/en/wiki/improv/status), [Haltung](/de/wiki/improtheater/haltung), [Figur](/de/wiki/improtheater/figur)\n\nSpielidee: Die typische und allen bekannte Situation  zwei Gruppen stehen sich an einer belebten (breiten!) Strae an den Fugngerampeln gegenber  wird als Impro gespielt. Eine dritte Gruppe beobachtet mglichst genau. Beide Gruppen stehen und schauen (Ampel, Gegenber, Strae). Auf den [Impuls](/de/wiki/improtheater/impuls) eines Spielers gehen alle los (grn), begegnen sich (Status), weichen aus, rempeln, etc. Alle nehmen dabei eine Haltung ein oder geben sich eine [Figur](/de/wiki/improtheater/figur)/Rolle. Es wird nichts verabredet! An den beiden Enden des Raumes wird umgedreht, an den Ampeln wieder angehalten, das Spiel beginnt von vorne (mit und ohne Sprache mglich). Anregung: Die Gesichter werden leer, man beobachtet die eigene Gruppe und die Gruppe gegenber, einer geht zu frh los und zuckt zurck, Autos werden mit dem Blick verfolgt und vielleicht kommentiert, ein LKW fhrt durch Pftze und vorne werden alle nass, es regnet, schneit, ist hei, Ampel geht kaputt  Die beiden imaginren Ampeln und der Zebrastreifen werden vor Spielbeginn installiert.\n",
