      "Alle laufen durch den Raum bis einer mit einem lauten Tritt auf den Boden stehen bleibt und auf einen Spieler zeigt.\n\nDann bleiben alle stehen. Derjenige, welcher zuerst stehen geblieben ist, nennt demjenigen, auf den er zeigt ein x-beliebiges Wort.Der nchste assoziiert etwas mit diesem Wort und nennt seine Assoziation und zeigt auf einen weiteren Spieler.\n\nDas geht solange, bis einer stockt. Dann laufen alle weiter und ein anderer bleibt stehen.\n\nDie Assoziationen sollten mglichst schnell geschehen.\n\nEs ist darauf zu achten, dass man wirklich auf das letztgenannte Wort assoziiert und nicht auf eins, was drei Stationen vorher gefallen ist.\n\nz.B. Kaffee - Bohne - Erbse - Prinzessin - Mrchen - Buch - Seite...\n\n_Siehe auch_ [Assoziieren](/de/wiki/improtheater/assoziieren)\n",
