    content:
      'Ein Spiel, um [Reimen](/de/wiki/improtheater/reimen) und Rhythmus in der Gruppe zu ben.\n\nAlle Spieler stellen sich im Kreis auf. Nun wird im Kreis im Uhrzeigersinn herum Wort fr Wort (jeder Spieler nur ein Wort) eine Bauernregel aufgesagt, nach dem (Reim-) Schema der bekannten Bauernregeln.\n\nBeispiel:\n\n*   "Wenn die Bume zweimal blhen, wird sich Winter bis Mai hinziehen."\n\nDiese bung kann aufgrund der Reime und des Rhythmus schwierig werden, deshalb wenn man sich verhaspelt, hier einfach nach dem "Scheiter heiter!"-Prinzip von vorne beginnen. Die Reime mssen nicht unbedingt alle Sinn machen. Der Spass und das gleichmssige Fliessen der Wrter stehen hier zunchst im Vordergrund. Trotzdem bietet es sich an ein wenig in Reimen vorauszudenken und es besonders dem Nebenmann mit den Wrtern nicht allzu schwer zu machen.\n\n### Variationen\n\n*   Statt Wort fr Wort knnen die Spieler auch nur Silbe fr Silbe sprechen, also "Wenn(1)-die(2)-Bu(3)-me(4)-zwei(5)-mal(6)-bl(7)-hen(8)..."\n',
