    content:
      "Ein Spieler stellt sich in die Mitte und schliet seine Augen. Die anderen Teilnehmer stehen oder gehen um Ihn herum. Dabei machen sie allerhand Gerusche (Wind, Tiere, Maschinen). Versucht dabei einen Spannungbogen zu entwickeln. Fangt langsam und leise an und hrt auch so auf. Whrend dieser bung darf niemand sprechen.\n",
