    content:
      "Zwei Personen treffen sich zum ersten Mal und erkennen, dass sie fr einander bestimmt sind!\n\nVorgaben:\n\n*   Geschlecht, Alter, Name und einen Beruf und / oder ein geheime Sehnsucht fr jede Person\n*   Der Ort an dem sie sich treffen.\n\nVariante: [Schizo Blind date](/de/wiki/improtheater/schizo_blind_date)\n\n_siehe auch:_ [Das verflixte siebte Jahr](/de/wiki/improtheater/das_verflixte_siebte_jahr)\n",
