      "Zwei Darsteller spielen auf der Bhne eine Szene und werden von zwei anderen Spielern, welche mit dem Rcken zur Bhne sitzen synchronisiert. Die Darsteller machen die passenden Mundbewegungen.\n\n**Video Blind Synchro:**  \n[Blind Synchro: Die Killerkartoffel (Die Impronauten aus Basel)](http://de.youtube.com/watch?v=eY3ceaCeYNg) [www.impronauten.ch](http://www.impronauten.ch)\n",
