    content:
      "Das ist eine musikalische Version des [Zettelspiels](/de/wiki/improtheater/zettelspiel): Das Publikum wird gebeten, einen einfache Satz auf zuvor zur Verfgung gestellte Zettel zu schreiben. Entweder werden die beschrifteten Zettel auf der Bhne verteilt oder die Spieler stecken sie in die Tasche. Jedenfalls nehmen die Spieler mehrfach whrend der Szene einen Zettel zur Hand und fangen unverzglich mit dem darauf stehenden Satz an, ein Lied zu singen. Der Satz sollte mglichst sinnvoll in die Szene integriert werden.\n",
