    content:
      "Der Moderator fragt nach einem Sternzeichen und liest dann aus einer Zeitschrift die aktuelle Horoskopvorhersage vor. Die Spieler spielen dann eine Szene, in denen alle Horoskop-Vorhersagen eintreffen und den Ratschlgen aus dem Horoskop gefolgt wird, so unsinnig sie im Kontext der Szene auch sein mgen.\n",
