      "Von den Zuschauern wird ein bekanntes Mrchen als Vorgabe eingeholt. Dieses Mrchen wird dann in einer neutralen Szene kurz gespielt.\n\nAnschlieend wird das Mrchen in einem anderen Genre wiederholt (siehe [Genreliste](/de/ideen-fuer-vorgaben/genreliste)), welches vorher vom Publikum bestimmt wurde.\n",
