    content:
      "Das [Publikum](/de/wiki/improtheater/zuschauer) gibt einen Titel vor.\n\nSmtliche Spieler tanzen in modernen Tnzen ber die [Bhne](/de/wiki/improtheater/buehne). Es gibt keinen Text, keinen Gesang, aber alles passt irgendwie zusammen.\n",
