      "Die Spieler stehen in einer Reihe mit der Front zu den Zuschauern. Jedem Spieler wird ein Radiosender zugeordnet. In zuflliger Reihenfolge wechseln nun die Sender (Spieler) und das Publikum hrt einen Ausschnitt des jeweiligen Programms. Musikalische Untersttzung ist hier sehr wichtig.\n",
