    content:
      "Alle Spieler stellen sich in eine Schlange. Der Erste tritt vor und singt ein beliebiges bekanntes Lied. Noch bevor ihm der Text ausgeht, strmt der Nchste vor, schiebt ihn von der [Bhne](/de/wiki/improtheater/buehne), behauptet, viel besser singen zu knnen und singt nun sein Lied. Der erste stellt sich in der Schlange wieder hinten an. usw.\n\nsiehe auch [Gesangsassoziation](/de/wiki/improtheater/gesangsassoziation), [Serenade II](/de/wiki/improtheater/serenade_ii)\n",
