    content:
      "Die Teilnehmer stehen im Kreis. Der Anleiter gibt den Text vor und die TN wiederholen diesen so laut wie sie knnen. I have a Mango - TN wiederholen - And my Body go relaxe relaxe relaxe (wird nach jedem Ausruf wiederholt, dabei geht der Krper in eine rhythmische Bewegung). I cut the Mango - TN wiederholen - And my Body go relaxe relaxe relaxe (wird nach jedem Ausruf wiederholt, dabei geht der Krper in eine rhythmische Bewegung). I eat the Mango - TN wiederholen - And my Body go relaxe relaxe relaxe (wird nach jedem Ausruf wiederholt, dabei geht der Krper in eine rhythmische Bewegung). I shit the Mango - TN wiederholen - And my Body go relaxe relaxe relaxe (wird nach jedem Ausruf wiederholt, dabei geht der Krper in eine rhythmische Bewegung). I clean the Mango - TN wiederholen - And my Body go relaxe relaxe relaxe (wird nach jedem Ausruf wiederholt, dabei geht der Krper in eine rhythmische Bewegung).\n\nGute bung um Energie aufzubauen und diese auch wieder los zulassen.\n",
