    content:
      "Man schreibt seinen Namen mit einem Krperteil in die Luft. Zunchst mit der Nase, dem Bauchnabel, dem Ellenbogen, dann vielleicht dem Ohr und dem Knie. Man kann seinen Namen von rechts nach links oder umgekehrt schreiben. Weiter kann versucht werden den Namen 'in den Raum' hineinzuschreiben.\n",
