    content:
      "Ein Spiel zum Kennenlernen. Alle Spieler stellen sich in einem Kreis auf. Dann erzhlt jeder seinen Namen und zwei Dinge. Eine Aussage ist wahr, die zweite ist gelogen. Wenn alle Spieler dran waren, fngt der erste Spieler an, zeigt auf jemanden und nennt dessen Name und die beiden Dinge. Dieser Spieler fhrt fort mit einem anderen Spieler und so weiter. Nach ein paar Runden werden dann alle zu allen Spielern befragt, was sie fr eine Lge halten und welche Aussage wohl der Wahrheit entspricht.\n",
